.channelRow{
    display: flex;
    align-items: center;
    width: 70%;
}
.channelRow__Text{
    display: flex;
    flex-direction: column;

}
.channelRow__Logo{
    height: 120px !important;
    width: 120px !important;
    margin: 10px 60px;
}
.channelRow__Text>p{
    color: #606060;
    font-size: small !important;
}
.channelRow__Text>h4{
    display: flex;
    align-items: center;
    justify-content: space-between;
}