.videoCard{
    width: 270px;
    margin-bottom: 40px;
    margin-left: 30px;
  
}
.thumbnail{
    height: 140px;
    width: 250px;
}
.video__info{
    display: flex;
    margin-top: 10px;
    padding-right: 30px;
    
}
.video__text{
    margin-left: 10px;
}
.video__text>h4{
    font-size: 14px;
    margin-bottom: 5px;
}
.video__text>p{
    font-size: 13px;
    color: gray;
}
.video__avatar{
    height: 30px !important;
    width: 30px !important;
}
.video__text > * {
    margin: 1px;
}